<template>
  <div class="playbar">
    <div class="playbar-box" @click="showLyriceScreen = true">
      <img
        
        class="img-box"
        v-if="(currentSong.picUrl || currentSong.al) !== undefined"
        :class="{active: playing}"
        :src="currentSong.picUrl || currentSong.al.picUrl"
        alt
      />
      <img
        v-else
        
        class="img-box"
        :class="{active: playing}"
        :src="currentSong.album.artist.img1v1Url"
        alt
      />
      <div class="play-info">
        <h5>{{currentSong.name}}</h5>
        <span
          class="singer"
          v-if="currentSong.song !== undefined"
        >{{joinArtists(currentSong.song.artists)}}</span>
        <div class="singer" v-else-if="currentSong.ar !== undefined">{{currentSong.ar[0].name}}</div>
        <div class="singer" v-else>{{currentSong.artists[0].name}}</div>
      </div>
      <div class="progress">
        <canvas width="36" height="36" id="canvas"></canvas>
        <div @click.stop="playing = !playing">
          <img v-if="playing" src="../assets/images/playbar_pause.png" alt />
          <img v-else src="../assets/images/playbar_play.png" alt />
        </div>
      </div>
      <div class="playlist" @click.stop="showPlayList = !showPlayList">
        <img src="../assets/images/playlist.png" alt />
      </div>
    </div>

    <!-- 播放列表 -->
    <transition
      name="custom-classes-transition"
      enter-active-class="animated slideInUp"
      leave-active-class="animated slideOutDown"
     >
      <div v-if="showPlayList" class="defaultPlayList">
        <List
          v-for="(item, index) in defaultPlayList"
          :key="index"
          :lists="item"
          :options="{order: index}"
          @translate-song="$emit('translate-song', $event)"
          :currentSong="currentSong"
        ></List>
      </div>
    </transition>

    <!-- 歌词 -->
    <transition
      name="custom-classes-transition"
      enter-active-class="animated fadeIn"
      leave-active-class="animated fadeOut"
     >
      <div v-show="showLyriceScreen" class="lyrice-box">
        <!-- 遮罩层 -->
        <div
          class="mask"
          v-if="(currentSong.picUrl || currentSong.al) !== undefined"
          :style="{backgroundImage: `url(${currentSong.picUrl || currentSong.al.picUrl})`}"
        ></div>
        <div
          v-else
          @click="showLyriceScreen = true"
          class="mask"
          :style="{backgroundImage: `url(${currentSong.album.artist.img1v1Url})`}"
        ></div>
        <!-- 头部 -->
        <div class="header">
          <img @click="showLyriceScreen = false" src="../assets/images/back.png" alt />
          <div class="title-box">
            <div class="title">{{currentSong.name}}</div>
            <div
              class="singername"
              v-if="currentSong.song !== undefined"
            >{{joinArtists(currentSong.song.artists)}}</div>
            <div
              class="singername"
              v-else-if="currentSong.ar !== undefined"
            >{{currentSong.ar[0].name}}</div>
            <div class="singername" v-else>{{currentSong.artists[0].name}}</div>
          </div>
          <img src="../assets/images/share.png" alt />
        </div>

        <!-- 内容区 磁盘 -->
        <div
          v-show="showDisk"
          class="disk"
          :class="{active: playing}"
          @click="showDisk = !showDisk"
         >
          <div class="content-img">
            <img
              v-if="(currentSong.picUrl || currentSong.al) !== undefined"
              :src="currentSong.picUrl || currentSong.al.picUrl"
              alt
            />
            <img v-else :src="currentSong.album.artist.img1v1Url" alt />
            <div class="disk-play" @click.stop="playing = !playing">
              <img v-if="playing" src="../assets/images/disk_pause.png" alt />
              <img v-else src="../assets/images/disk_play.png" alt />
            </div>
          </div>
          <div class="needle"></div>
        </div>

        <!-- 内容区 歌词 -->
        <div v-show="!showDisk" @click="showDisk = !showDisk" class="lyric-box">
          <ul class="scroll" :style="{transform: `translateY(${-currentLyricIndex * 28}px)`}">
            <li
              v-for="(item, index) in parsedLyric"
              :class="{active: index === currentLyricIndex}"
              :key="index"
            >{{item.text}}</li>
          </ul>
        </div>

        <!-- 底部 播放条 -->
        <div class="footer">
          <div class="propress-box">
            <div class="disk-progress">
              <div class="pro-box">
                <div class="current-time">{{time}}</div>
                <div class="progress-notactive">
                  <!-- 激活层 -->
                  <div class="progress-active" :style="{width: width}"></div>
                  <!-- 滑块 -->
                  <div class="disk-mask" :style="{left: diskMaskLeft}" :class="{play: playing}"></div>
                </div>
                <div class="duration">{{songTime}}</div>
              </div>
              <!-- 绑定事件层 -->
              <div class="layer" @touchstart="touchstart"></div>
            </div>
            <div class="play-box">
              <div class="play-mode">
                <img class="img-auto" src="../assets/images/shunxu.png" alt />
              </div>
              <div class="play-img">
                <div class="prev-box">
                  <img class="img-auto" src="../assets/images/prev.png" alt />
                </div>
                <div class="play-pause" >
                  <img v-if="playing" class="img-auto" src="../assets/images/disk_pause.png" @click="playing = !playing" alt />
                  <img v-else class="img-auto" src="../assets/images/disk_play.png" @click="playing = !playing" alt />
                </div>
                <div class="next-box">
                  <img class="img-auto" src="../assets/images/next.png" alt />
                </div>
              </div>
              <div class="list">
                <img
                  class="img-auto"
                  src="../assets/images/disk_playlist.png"
                  @click="showPlayList = !showPlayList"
                  alt
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>

    <audio
      v-if="(currentSong.dt || currentSong.song) !== undefined"
      :time="currentSong.dt || currentSong.song.duration"
      :src="currentSongUrl"
      autoplay
    ></audio>
    <audio v-else :time="currentSong.duration" :src="currentSongUrl" autoplay></audio>
  </div>
</template>

<script>
import List from "../components/List";
// import PlayProgress from '../components/PlayProgress'

export default {
  name: "PlayBar",

  components: {
    List
    // PlayProgress
  },

  props: {
    currentSong: Object
  },

  data() {
    return {
      playing: false,
      showLyriceScreen: false,
      test: {},
      showDisk: true,
      currentLyric: null,
      currentLyricIndex: 0,
      defaultPlayList: [this.currentSong],
      showPlayList: false,
      isPlay: false,
      diskMaskLeft: "0px",
      width: "0px",
      time: '',
      songTime: ''
    };
  },

  computed: {
    currentSongUrl() {
      if (this.currentSong) {
        return `https://music.163.com/song/media/outer/url?id=${this.currentSong.id}.mp3`;
      } else {
        return "";
      }
    },

    parsedLyric() {
      if (this.currentLyric) {
        return this.currentLyric.split("\n").map(item => {
          // console.log(item);
          var regex = /\d{2}:\d{2}\.\d+/i;
          if (item.search(regex) !== -1) {
            var time = item.match(regex)[0];
            var m = time.substr(0, 2);
            var s = time.substr(3, 2);
            var n = time.substr(5, 3);
            // console.log(m, s, n);
            var l = time.length;

            if (l == 8) {
              return {
                time: Number(m) * 60 + Number(s) + Number(n),

                text: item.substr(10) || "---------"
              };
            } else if (l == 9) {
              return {
                time: Number(m) * 60 + Number(s) + Number(n),

                text: item.substr(11) || "---------"
              };
            }
          } else {
            return {};
          }
        });
      } else {
        return null;
      }
    }
  },

  methods: {
    joinArtists(artists) {
      return artists.map(artist => artist.name).join(" / ");
    },

    drawProgress() {
      var canvas = this.$el.querySelector("#canvas");

      var audio = this.$el.querySelector("audio");

      audio.ontimeupdate = () => {
        let dMask = this.$el.querySelector(".disk-mask");
        let maxLeft = this.$el.querySelector(".progress-notactive").clientWidth - dMask.clientWidth;
        // console.log('maxLeft ==>', maxLeft);
        

        let progressWidth = this.$el.querySelector(".progress-notactive").clientWidth;
        // console.log('progressWidth ==>', this.$el.querySelector(".progress-notactive"));


        let currentTime = audio.currentTime;
        let ct = this.formatTime(currentTime);
        // console.log("ct ==>", ct);
        this.time = ct;

        this.songTime = this.formatTime(audio.duration);

        let percent = currentTime / audio.duration;

        this.width = progressWidth * percent + "px";

        this.diskMaskLeft = maxLeft * percent + "px";
        // console.log('canvas == >', canvas);

        // 获取canvas元素
        var context = canvas.getContext("2d");
        context.clearRect(0, 0, 36, 36);
        context.beginPath();
        context.arc(19, 19, 17, 0, 2 * Math.PI);
        context.closePath();
        context.strokeStyle = "#bfbfbf";

        // 描边样式
        context.lineWidth = "1";

        // 描边宽度
        context.stroke(); // 路径描边
        context.beginPath();
        context.arc(
          19,
          19,
          17,
          -0.5 * Math.PI,
          -0.5 * Math.PI + 2 * Math.PI * (audio.currentTime / audio.duration)
        );

        // context.closePath();
        context.strokeStyle = "rgba(255,0,0,0.7)";

        // 描边样式
        context.lineWidth = "1";

        // 描边宽度
        context.stroke(); // 路径描边

        let index;
        for (let i = 0; i < this.parsedLyric.length; i++) {
          if (audio.currentTime + 0.15 < this.parsedLyric[i].time) {
            index = i - 1;
            break;
          }
        }

        if (index === undefined) {
          index = this.parsedLyric.length - 1;
        }

        this.currentLyricIndex = index;
      };
    },

    getLyric() {
      var cachedLyric = window.localStorage.getItem(
        "lyric" + this.currentSong.id
      );
      if (cachedLyric) {
        this.currentLyric = cachedLyric;
      } else {
        this.axios
          .get("https://music.kele8.cn/lyric", {
            params: {
              id: this.currentSong.id
            }
          })
          .then(response => {
            // console.log(response);

            this.currentLyric = response.data.lrc.lyric;
            window.localStorage.setItem(
              "lyric" + this.currentSong.id,
              response.data.lrc.lyric
            );
          });
      }
    },

    touchstart(event) {
      // this.touchstartX = event.changedTouches[0].clientX;

      // 鼠标点击所在位置
      let x = event.changedTouches[0].clientX;
      // console.log("x ==>", x);

      let dMask = this.$el.querySelector(".disk-mask");
      // console.log(dMask);

      let maxLeft = this.$el.querySelector(".progress-notactive").clientWidth - dMask.clientWidth;
      // console.log("maxLeft ==>", maxLeft);

      let l = this.$el.querySelector(".current-time").clientWidth;

      this.width = x - l + "px";

      let minLeft = 0;

      // 鼠标在滑块中间位置
      let left = x - dMask.clientWidth / 2;
      // console.log('1:', dMask.clientWidth / 2);

      // console.log('left ==>', left);

      // 滑块位置
      left = left > maxLeft ? maxLeft : left < minLeft ? minLeft : left;

      // console.log("left ==>", left);

      this.diskMaskLeft = left - l + "px";

      // console.log('this.diskMaskLeft ==>', this.diskMaskLeft);
    },

    formatTime(time) {
      var minute = Math.floor(time / 60);
      minute = minute >= 10 ? minute : "0" + minute;

      var second = Math.floor(time % 60);
      second = second >= 10 ? second : "0" + second;

      return minute + ":" + second;
    }
  },

  mounted() {
    this.drawProgress();

    var audio = this.$el.querySelector("audio");
    // console.log('audio ==>', audio);

    audio.onplay = () => {
      // console.log("开始播放");
      this.playing = true;
      audio.duration;
      // console.log(audio.duration);
      // this.touchstart(event);
    };
    audio.onpause = () => {
      // console.log("暂停播放");
      this.playing = false;

    };

      // audio.ontimeupdate = () => {
      //   this.drawProgress();
      //   // console.log(111);
      // };
  },

  watch: {
    playing(value) {
      var audio = this.$el.querySelector("audio");

      if (value) {
        audio.play();
      } else {
        audio.pause();
      }
    },
    currentSong(value) {
      this.getLyric();
      // 加入列表 需要过滤重复
      // this.defaultPlayList.push(value);
      let isExist = this.defaultPlayList.some(item => {
        return item.id == value.id;
      });
      // console.log(isExist);
      if (!isExist) this.defaultPlayList.push(value);
    }
  },

  created() {
    this.getLyric();
  }
};
</script>

<style lang="less" scoped>
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

@keyframes shadow {
  0% {
    box-shadow: 0px 0px 20px 5px rgba(255, 255, 255, 0);
  }

  100% {
    box-shadow: 0px 0px 20px 5px rgba(255, 255, 255, 1);
  }
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.playbar {
  border-top: 1px solid #ddd;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #fff;
  .playbar-box {
    display: flex;
    padding: 5px;
    z-index: 10;

    .play-info {
      // padding: 5px 0;
      flex: 1;

      h5 {
        margin: 0;
      }

      .singer {
        font-size: 12px;
        color: #a8a8a9;
      }
    }

    .img-box {
      width: 40px;
      height: 40px;
      display: block;
      margin: 0 5px;
      border-radius: 50%;
      animation: rotate 6s linear infinite;
      animation-play-state: paused;
      &.active {
        animation-play-state: running;
      }
    }

    .progress {
      width: 40px;
      height: 40px;
      position: relative;
      // background-color: rosybrown;
      // padding: 5px 0;

      img {
        width: 15px;
        height: 15px;
        display: block;
        // padding: 6px;
        position: absolute;
        top: 12.9px;
        left: 13.4px;
      }

      #canvas {
        width: 28px;
        height: 28px;
        margin: 6px;
      }
    }

    .playlist {
      width: 40px;
      height: 40px;
      // background-color: #ddd;
      // padding: 5px 0;

      img {
        width: 25px;
        height: 25px;
        display: block;
        padding: 6px;
      }
    }
  }

  .defaultPlayList {
    position: fixed;
    max-height: 250px;
    bottom: 110px;
    left: 2.5%;
    width: 95%;
    background: #fff;
    border-radius: 15px;
    box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.3);
    z-index: 9999;
    overflow-y: auto;

    .box {
      display: flex;
      margin: 5px 10px;

      .item-info {
        // width: 90%;
        flex: 1;
        // margin: 5px 0;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;

        .item-name {
          height: 24px;
          line-height: 30px;
          font-size: 16px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;

          span {
            margin-left: 4px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            color: #a8a8a9;
          }
        }

        .singer {
          font-size: 10px;
          color: #a8a8a9;
        }
      }

      .play {
        width: 36px;

        img {
          width: 22px;
          height: 22px;
          display: block;
          padding: 9px 6px;
        }
      }
    }
  }

  .lyrice-box {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    display: flex;
    flex-direction: column;

    .mask {
      filter: blur(30px) brightness(0.5);
      transform: scale(2);
      width: 100%;
      height: 100%;
      background-size: cover;
      background-position: center;
      background-color: rgba(0, 0, 0, 0.3);
      position: absolute;
      z-index: -1;
    }

    .header {
      height: 35px;
      display: flex;
      margin: 10px 5px;
      img {
        width: 20px;
        height: 20px;
        display: block;
        margin: 5px;
      }

      .title-box {
        flex: 1;
        color: #fff;
        text-align: center;

        .title {
          font-size: 14px;
        }

        .singername {
          font-size: 12px;
        }
      }
    }

    .disk {
      text-align: center;
      position: relative;
      flex: 1;

      .content-img {
        // background-color: #000;
        background: url("../assets/images/disc_light.png"),
          url("../assets/images/disc-ip6.png") no-repeat;
        background-size: 100%;
        text-align: center;
        padding: 55px;
        border-radius: 50%;
        display: inline-block;
        margin-top: 85px;
        position: relative;

        img {
          width: 180px;
          height: 180px;
          border-radius: 50%;
          animation: rotate 6s linear infinite;
          animation-play-state: paused;
          // filter: brightness(0.6);
        }

        .disk-play {
          position: absolute;
          top: 50%;
          left: 50%;
          margin-top: -16px;
          margin-left: -16px;

          img {
            width: 32px;
            height: 32px;
            display: block;
          }
        }
      }

      .needle {
        position: absolute;
        width: 96px;
        height: 137px;
        background: url("../assets/images/needle.png");
        background-size: 100%;
        top: 0;
        left: 50%;
        margin-left: -18px;
        transform: rotate(-10deg);
        transform-origin: 16px 0;
      }

      &.active {
        .needle {
          transform: rotate(0deg);
        }
        .content-img > img {
          animation-play-state: running;
        }
      }
    }

    .lyric-box {
      flex: 1;
      overflow: hidden;
      position: relative;
      .scroll {
        text-align: center;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 50%;
        margin-top: -1em;
        transition: transform 0.3s;
        li {
          line-height: 2em;
          font-size: 14px;
          height: 2em;
          color: rgba(184, 167, 167, 0.877);
          &.active {
            color: white;
          }
        }
      }
    }

    .footer {
      height: 120px;
      // background-color: rosybrown;
      background-color: rgba(0, 0, 0, 0.3);

      .img-auto {
        width: 100%;
        display: block;
      }
      .propress-box {
        .disk-progress {
          position: relative;
          .pro-box {
            display: flex;
            width: 100%;
            height: 15px;
            padding-bottom: 3px;
            position: relative;
            font-size: 12px;
            color: #fff;
            // margin-top: 20px;
            // margin-left: calc((100% - 300px) / 2);
            margin: 10px auto;
            .progress-notactive {
              width: 300px;
              height: 5px;
              background-color: #a19492;
              position: relative;
              z-index: 15;
              margin-top: 5px;
            }
            .progress-active {
              position: absolute;
              top: 0;
              left: 0;
              // width: 150px;
              height: 5px;
              // background-image: linear-gradient(to right, #bf560c, #852314);
              background-color: #fff;
            }

            .current-time {
              width: calc((100% - 300px) / 2 - 10px);
              padding: 0 5px;
            }

            .duration {
              width: calc((100% - 300px) / 2 - 10px);
              padding: 0 5px;
            }

            .disk-mask {
              position: absolute;
              width: 10px;
              height: 10px;
              border-radius: 50%;
              background-color: #ffffff;
              top: -2.5px;
              // left: 0px;

              &.play {
                animation: shadow 1s linear 0s infinite alternate;
              }
            }
          }

          .layer {
            position: absolute;
            left: 50%;
            top: 0;
            width: 300px;
            height: 15px;
            z-index: 2;
            margin-left: -150px;
          }
        }

        .play-box {
          padding: 0 10px;
          display: flex;
          .play-mode {
            img {
              width: 25px;
              margin-top: 10px;
            }
          }

          .play-img {
            flex: 1;
            display: flex;

            .prev-box {
              width: 100px;
              margin: auto;

              img {
                width: 32px;
                margin: auto;
              }
            }

            .play-pause {
              flex: 1;
              img {
                width: 45px;
                margin: auto;
              }
            }

            .next-box {
              width: 100px;
              margin: auto;
              img {
                width: 32px;
                margin: auto;
              }
            }
          }

          .list {
            img {
              width: 25px;
              margin-top: 10px;
            }
          }
        }
      }
    }
  }
}
</style>